body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "Karla", sans-serif;
}

.header {
  display: flex;

  background: linear-gradient(90deg, #672280 1.18%, #a626d3 100%);
  color: white;
}

.header > img {
  width: 40px;
  margin: 15px 5px 15px 15px;
}

.header > span {
  font-size: 25px;
  margin: 15px 0;
}


.meme {
  margin-top: 15px;
  display: flex;
  justify-content: center;
}

.meme__inputs {
  margin-top: 10px;
  padding: 10px;
  margin-right: 10px;
  width: 40%;
  margin-bottom: 10px;
}

.meme__button {
  width: 91%;
  height: 40px;
  color: white;
  background: linear-gradient(90.41deg, #711f8d 1.14%, #a818da 100%);
  border-radius: 5px;
  border: none;
}

.meme__wrap{
  position: relative;
}

.meme__image{
  margin-top: 10px;
  display: block;
  width: 490px; 
}

.text__button{
  margin-top: 10px;
  width: 90%;
  height: 35px;
  background-color: #211426;
  color: white;
  border-radius: 5px;
}

.meme--text {
  cursor: pointer;
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);  
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 1.5em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow:
      2px 2px 0 #000,
      -2px -2px 0 #000,
      2px -2px 0 #000,
      -2px 2px 0 #000,
      0 2px 0 #000,
      2px 0 0 #000,
      0 -2px 0 #000,
      -2px 0 0 #000,
      2px 2px 5px #000; 
}


